.Card{position:relative;display:flex;flex-direction:column;justify-content:flex-start;max-height:300px;margin:15px 20px 0 0;padding:5px;border-radius:12px;background:#18181810;cursor:pointer;user-select:none;transition:all .35s}.Card:hover,.Card:focus{background:#18181840;transform:scale(1.1);z-index:1}.Card:hover::before{position:absolute;top:38%;padding:0 8px;width:100%;height:100%;content:"View Details";color:#181818;font-size:1.75rem;font-weight:bolder;z-index:2;transition:.35s}.Card:active{transform:scale(1)}.Card:hover .Card__img{opacity:.5}.Card .Card__img{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:12px;width:180px;height:200px;padding:30px 50px;background:#fff}.Card .Card__img img{width:100px;opacity:.85;transition:.35;z-index:1}.Card:hover img,.Card:focus img{transform:scale(1.2);opacity:.5}.Card .Card__info{display:flex;flex-direction:column;justify-content:space-between;width:100%;padding:10px 15px;color:#252525}.Card .Card__info h3{margin:4px 0;padding:0;max-width:70%;font-size:.8rem;font-weight:normal}.Card .Card__info .Card__info--price{color:#181818;font-size:.9rem;font-weight:bold}.Card .Card__info .Card__info--price::before{content:"€";font-size:.6rem;color:#252525}