.Modal{position:fixed;top:57px;right:0;bottom:0;left:0;display:flex;justify-content:center;align-items:center;max-width:100vw;height:100vh;background:rgba(255,255,255,.85);animation:fadeIn .35s;z-index:3}.Modal .Modal--overlay{position:fixed;top:0;right:0;bottom:0;left:0;max-width:100vw;height:100vh;cursor:pointer;z-index:-1}.Modal .Modal__content{padding:40px;display:flex;flex-direction:row;justify-content:center;align-items:center;max-width:720px;height:100%;background:#fff;border-radius:12px;box-shadow:0 0 4px #18181825;overflow-y:auto;animation:fadeIn .75s}.Modal .Modal__content .data{margin:0 60px 0 40px}.Modal .Modal__content .data h1{font-size:1.6rem}.Modal .Modal__content .data p{color:gray;font-size:.9rem;font-weight:normal;line-height:20px;word-spacing:2.5px}.Modal .Modal__content .data .price{display:flex}.Modal .Modal__content .image img{width:150px}@media(min-width: 676px){.Modal .Modal__content{height:70vh}}.Modal .Modal__content .Modal__content--price{margin:0;font-size:2rem}.Modal .Modal__content .Modal__content--price::after{content:"€";font-size:1.2rem}.Modal .closeButton{position:absolute;top:50px;right:25px;width:20px;height:20px;color:#2ac59e;font-family:Courier New,Courier,monospace;font-weight:bolder;font-size:1.8rem;text-shadow:1px 2px 1px #18181879;cursor:pointer;transition:all .35s}.Modal .closeButton:hover,.Modal .closeButton:focus{transform:scale(1.4)}.Modal .closeButton:active{transform:scale(1)}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}