@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,200;0,400;0,600;0,700;1,200;1,400;1,600;1,700&display=swap);
*{box-sizing:border-box}html{font-size:16px}body{position:relative;margin:0;padding:0;font-family:"Josefin Sans",sans-serif}
.Header{position:fixed;display:flex;flex-direction:row;justify-content:space-between;width:100%;padding:0 25px;background-color:#fff;box-shadow:0 0 2px 1px #1818185d;z-index:9}.Header .Header__logo{width:200px;-webkit-filter:invert(60%) sepia(0%) saturate(493%) hue-rotate(178deg) brightness(100%) contrast(450%);filter:invert(60%) sepia(0%) saturate(493%) hue-rotate(178deg) brightness(100%) contrast(450%)}.Header .chart{display:flex;align-items:center}.Header .chart .chart--shopping{height:25px}.Header .chart .chart--total{position:relative;left:-10px}.Header .chart .chart--elsInChart{position:absolute;right:5px;border-radius:100%;padding:2.8px 7px;color:#1818189a;font-size:1.2rem}
.chart--shopping{cursor:pointer}
.Hero{position:relative;display:flex;flex-direction:row;align-items:center;justify-content:space-between;height:100vh;margin:0}.Hero .Hero__image img{width:100%;height:101vh;object-fit:cover;-webkit-filter:grayscale(70%) contrast(105%);filter:grayscale(70%) contrast(105%)}@media(max-width: 676px){.Hero .Hero__image{position:absolute;opacity:.45;-webkit-filter:contrast(100%);filter:contrast(100%);z-index:-1}}.Hero .Hero__info{display:flex;flex-direction:column;justify-content:center;margin:20px 80px}.Hero .Hero__info h1{color:#181818;font-size:3rem;font-weight:normal}.Hero .Hero__info p{position:relative;top:-87px;left:130px;width:65%;font-size:.9rem}.Hero .Hero__info p::before{position:absolute;top:0;left:-5px;content:"";width:1px;height:28px;background:#181818}
.SearchField{display:flex;width:100%}.SearchField>*{margin:10px}
.Categories{display:flex;flex-direction:row;align-items:center;flex-wrap:wrap;width:100%}.Categories .tag{border:1px solid #18181850;margin:10px;padding:5px 10px;border-radius:3px;cursor:pointer}.Categories .tag::before{content:"x";padding-right:5px;font-weight:bolder;color:#ff000077}.selectedTag{background:#20f3be;border:1px solid #fff}
.Card{position:relative;display:flex;flex-direction:column;justify-content:flex-start;max-height:300px;margin:15px 20px 0 0;padding:5px;border-radius:12px;background:#18181810;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition:all .35s}.Card:hover,.Card:focus{background:#18181840;transform:scale(1.1);z-index:1}.Card:hover::before{position:absolute;top:38%;padding:0 8px;width:100%;height:100%;content:"View Details";color:#181818;font-size:1.75rem;font-weight:bolder;z-index:2;transition:.35s}.Card:active{transform:scale(1)}.Card:hover .Card__img{opacity:.5}.Card .Card__img{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:12px;width:180px;height:200px;padding:30px 50px;background:#fff}.Card .Card__img img{width:100px;opacity:.85;transition:.35;z-index:1}.Card:hover img,.Card:focus img{transform:scale(1.2);opacity:.5}.Card .Card__info{display:flex;flex-direction:column;justify-content:space-between;width:100%;padding:10px 15px;color:#252525}.Card .Card__info h3{margin:4px 0;padding:0;max-width:70%;font-size:.8rem;font-weight:normal}.Card .Card__info .Card__info--price{color:#181818;font-size:.9rem;font-weight:bold}.Card .Card__info .Card__info--price::before{content:"€";font-size:.6rem;color:#252525}
.CardList{display:flex;flex-direction:row;justify-content:flex-start;margin:50px 0 100px;padding:5px 20px;overflow-y:hidden}@media(min-width: 676px){.CardList{justify-content:center;flex-wrap:wrap}}
.Loader{display:flex;justify-content:space-evenly;align-items:center;color:#181818}.lds-facebook{display:inline-block;position:relative;width:80px;height:80px}.lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:#16e4b0;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}.lds-facebook div:nth-child(1){left:8px;animation-delay:-0.24s}.lds-facebook div:nth-child(2){left:32px;animation-delay:-0.12s}.lds-facebook div:nth-child(3){left:56px;animation-delay:0}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}
.DataFail{display:flex;flex-direction:row;justify-content:space-around;align-items:center;position:fixed;bottom:0;width:100%;background:#f0a59b;color:#fff;margin:25px 0 0 0}.DataFailHidden{display:none}
.Footer{display:flex;flex-direction:row;justify-content:space-between;width:100%;padding:0 25px;box-shadow:0 -4px 12px #1818184d}
.GenericModal{position:fixed;top:0;left:0;right:0;bottom:0;height:100vh;display:flex;justify-content:flex-end;z-index:9}.GenericModal .GenericModal--overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:#fff;cursor:pointer;opacity:.75;z-index:-1}.GenericModal .GenericModal__content{display:flex;flex-direction:column;align-items:center;width:100%;background:#fff;border-left:1px solid #18181825}@media(min-width: 676px){.GenericModal .GenericModal__content{width:65%}}.GenericModal .GenericModal__content header{display:flex;justify-content:space-between;align-items:center;padding:5px 50px;width:100%}.GenericModal .GenericModal__content header p{margin:2.5px;font-weight:bold;font-size:1.2rem}
.ShoppingCart{display:flex;flex-direction:column;overflow:auto;width:100%;padding:0 20px 100px}@media(min-width: 676px){.ShoppingCart{flex-direction:row;flex-wrap:wrap}}.ShoppingCart header{display:flex;justify-content:space-around;align-items:center;width:100px}.ShoppingCart article{position:relative;display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:10px;width:100%}.ShoppingCart article h3{font-size:1rem}@media(min-width: 676px){.ShoppingCart article{margin:10px 0}}.ShoppingCart article .image::before{position:absolute;bottom:35px;left:-45px;content:"";width:85px;height:50px;border-bottom:2px solid #0000001e;border-radius:20px;background:#1bebc8;opacity:.35}@media(min-width: 676px){.ShoppingCart article .image::before{width:50px}}.ShoppingCart article .image img{width:40px;height:80px;margin:0 40px 0 0;object-fit:contain}@media(min-width: 676px){.ShoppingCart article .image img{width:85px}}.ShoppingCart article .content{display:flex;flex-direction:column;justify-content:center;width:100%}.ShoppingCart article .content h3,.ShoppingCart article .content p{margin:0;padding:5px 0;font-size:.85rem}@media(min-width: 676px){.ShoppingCart article .content h3,.ShoppingCart article .content p{font-size:1rem}}.ShoppingCart article .buttons{display:flex;flex-direction:column;margin-top:-10px}.ShoppingCart article .buttons .buttons__quantity{display:flex;flex-direction:row;justify-content:center;align-items:center}.ShoppingCart article .buttons .buttons__quantity button{border-radius:100%;outline:none;border:0;width:20px;height:20px;font-weight:bold;pointer:cursor;margin:0 2.5px}@media(min-width: 676px){.ShoppingCart article .buttons .buttons__quantity button{width:25px;height:25px}}.ShoppingCart article .buttons .removeBtn{border:0;border-radius:4px}@media(min-width: 676px){.ShoppingCart article .buttons .removeBtn{padding:5px 10px}}.ShoppingCart footer{position:fixed;bottom:0;right:0;padding:0 10px;width:100%;background:#fff;border-left:1px solid #18181825}@media(min-width: 676px){.ShoppingCart footer{width:65%}}.ShoppingCart footer p{font-weight:bold;text-align:right}
.Modal{position:fixed;top:57px;right:0;bottom:0;left:0;display:flex;justify-content:center;align-items:center;max-width:100vw;height:100vh;background:rgba(255,255,255,.85);animation:fadeIn .35s;z-index:3}.Modal .Modal--overlay{position:fixed;top:0;right:0;bottom:0;left:0;max-width:100vw;height:100vh;cursor:pointer;z-index:-1}.Modal .Modal__content{padding:40px;display:flex;flex-direction:row;justify-content:center;align-items:center;max-width:720px;height:100%;background:#fff;border-radius:12px;box-shadow:0 0 4px #18181825;overflow-y:auto;animation:fadeIn .75s}.Modal .Modal__content .data{margin:0 60px 0 40px}.Modal .Modal__content .data h1{font-size:1.6rem}.Modal .Modal__content .data p{color:gray;font-size:.9rem;font-weight:normal;line-height:20px;word-spacing:2.5px}.Modal .Modal__content .data .price{display:flex}.Modal .Modal__content .image img{width:150px}@media(min-width: 676px){.Modal .Modal__content{height:70vh}}.Modal .Modal__content .Modal__content--price{margin:0;font-size:2rem}.Modal .Modal__content .Modal__content--price::after{content:"€";font-size:1.2rem}.Modal .closeButton{position:absolute;top:50px;right:25px;width:20px;height:20px;color:#2ac59e;font-family:Courier New,Courier,monospace;font-weight:bolder;font-size:1.8rem;text-shadow:1px 2px 1px #18181879;cursor:pointer;transition:all .35s}.Modal .closeButton:hover,.Modal .closeButton:focus{transform:scale(1.4)}.Modal .closeButton:active{transform:scale(1)}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}
.ShoppingCartModal{position:fixed;top:50px;left:0;right:0;bottom:0;display:flex;justify-content:flex-end;z-index:1}.ShoppingCartModal .ShoppingCartModal--overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:#fff;cursor:pointer;opacity:.75;z-index:-1}.ShoppingCartModal .ShoppingCartModal__content{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;background:#fff;box-shadow:0 0 8px 1px #1818185d}@media(min-width: 676px){.ShoppingCartModal .ShoppingCartModal__content{width:65%}}.ShoppingCartModal .ShoppingCartModal__content header{display:flex;justify-content:space-around;align-items:center;width:100px}.ShoppingCartModal .ShoppingCartModal__content main{overflow-y:auto;border:1px solid;width:250px}.ShoppingCartModal .ShoppingCartModal__content main .image img{width:100px}.ShoppingCartModal .ShoppingCartModal__content main .content{display:flex;justify-content:center;align-items:center}
.Search{display:flex;flex-direction:column;justify-content:center;align-items:center;transform:translateY(-40px)}.Search .SearchField input{height:20px}.blockScroll{position:fixed;overflow:hidden}
